                                <!-- <a v-if="sendForApprovalButton" class="float-right text-secondary" @click="Edit(dataIndex + 1)"><em class="fa fa-pencil" :title="$t('Edit')"></em></a> -->

                            </td>
                            <td class="remove_td align-middle d-none"></td>
                            <td class="align-middle d-none">{{dataItem['TaskDetail']}}</td>
                            <td class="align-middle" v-for="(item, index) in HeaderList" :key="index"
                                :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item , 'legend_leaves ' : checkLegendsDates(item)}"
                                :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                                <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                                <input placeholder="00:00" class="form-control text-center" type="text"
                                    v-model="dataItem[item]" @change="validateTimeInput(dataItem, item,index)"
                                    @focus="setOldValue(item ,$event)">
                                <div class="d-none">{{ dataItem[item] }} </div>
                            </td>
                            <td class="align-middle text-center totalvalue">{{ rowSum(dataItem) }}</td>
                            <td class="remove_td single-action text-center"><a><i class="fa fa-times text-danger"
                                        @click="SingleDeleteData(dataIndex)"></i></a> </td>
                        </tr>

                        <tr class="its_fixedrow" v-for="(dataItem, dataIndex) in computedBreakList" :key="dataIndex">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                            <td class="remove_td align-middle "></td>
                            <td class=" align-middle d-none"></td>
                            <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                            <td class="align-middle font-weight-medium">{{ dataItem['ProjectName'] }}</td>
                            <td class="remove_td align-middle d-none"></td>
                            <td class="remove_td align-middle d-none"></td>
                            <td class="align-middle" v-for="(item, index) in HeaderList" :key="index"
                                :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item }"
                                :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                                <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                                <input placeholder="" @keyup="timeMask" maxlength="5" value="00:00"
                                    @change="validateTimeInput(dataItem, item,index)" @focus="setOldValue(item ,$event)"
                                    class="form-control text-center" type="text" v-model="dataItem[item]">
                                <div class="d-none">{{ dataItem[item] }}</div>
                            </td>
                            <td class="align-middle text-center totalvalue">{{ rowSum(dataItem) }}</td>
                            <td class="remove_td single-action text-center"><i class="fa fa-times text-danger d-none"
                                    @click="SingleDeleteData(dataIndex)"></i> </td>
