                    </tfoot>
                </table>
            </div>
            <div class="col-lg-12 col-sm-12 p-0 mt-2">
                <a v-if="sendForApprovalButton" id="btnSendForApproval" class="btn btn-success formbtn text-light"
                    v-on:click="SaveAsDraft(0, 1)" name="" :title="$t('SendForApproval')" style="float: right;"><i
                        class="fa fa-send pr-2"></i>{{
                    $t('SendForApproval') }} </a>
                <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a> -->
                <a v-if="copyToOtherUserButton" id="btnCopyLastWeek" isapprover="0"
                    class="btn btn-warning formbtn mr-2 text-dark" v-on:click="showCopyTimesheet" name=""
                    :title="$t('CopyToOtherUser')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                    $t('CopyToOtherUser') }} </a>

                <a v-if="copyLastWeekButton" id="btnCopyLastWeek" isapprover="0"
                    class="btn btn-warning formbtn mr-2 text-dark" v-on:click="CopyLastWeek" name=""
                    :title="$t('CopyLastWeek')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                    $t('CopyLastWeek') }} </a>

                <a v-if="saveAsDraftButton" id="btnSaveAsDraft" isapprover="0"
                    class="btn btn-warning formbtn text-dark mr-2" v-on:click="SaveAsDraft(1, 0)" name=""
                    :title="$t('SaveAsDraft')" style="float: right;"><i class="fa fa-save pr-2"></i>{{ $t('SaveAsDraft')
                    }}</a>
                <!-- <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
            </div>
        </div>

        <copyTimesheet v-if="openModal" v-on:close="openModal = false"></copyTimesheet>

    </section>
</template>

<script>
    import DataService from '../../services/DataService';
    import {
        getPreviousDay,
        getDateInFormat,
        dateFormat,
        dateAndTimeZone, dateTimeFromat
    } from "@/assets/common/vue-common.js"
    import AddTaskTimeSheetModal from "../TimeTrack/modals/AddTaskTimesheetModal.vue"
    import DatePicker from 'v-calendar/lib/components/date-picker.umd'
    import copyTimesheet from "../../components/Modal/Timesheet/CopyTimesheet.vue"
    import {
        userTimerInfo
    } from "../../stores/TimeTrack"
    import moment from "moment"
    export default {
        components: {
            AddTaskTimeSheetModal,
            DatePicker,
            copyTimesheet
        },
        setup() {
            const store = userTimerInfo()

            return {
                store
            }
        },
        data() {
            return {
                dateMask: {
                    input: dateFormat,
                },
                saveAsDraftButton: true,
                copyLastWeekButton: true,
                copyToOtherUserButton: true,
                sendForApprovalButton: true,
                getPreviousDay,
                dateselect: dateAndTimeZone(moment(), dateTimeFromat),
                //formData: [],
                deletedArray: [],
                totalData: [],
                DataLoaded: 0,
                noRecord: false,
                TotalRecords: 0,
                isLoading: false,
                TimesheetList: [],
                BreakList: [],
                LeaveList: [],
                HeaderList: [],
                UserId: null,
                StartDate: null,
                EndDate: null,
                DisplayWeekDate: this.$t('thisWeek'),
                StartWeekDay: null,
                totalSeconds: 0,
                //Headers: [],
                addTaskShow: false,
                openModal: false,
                SumOfRows: [],
                SumOfFooterRows: 0,
                allCheck: '',
                listCheckbox: [],
                FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                        astype: "SlotField",
                        label: "",
                        name: "slotdate",
                        value: "",
                        placeholder: "Date Range",
                    },
                    {
                        astype: "SelectField",
                        label: "User",
                        name: "user_id",
                        value: "",
                        placeholder: "",

                        config: {
                            options: [],
                            onChange: this.FetchData
                        },
                        validationRules: {
                            //"required": true
                        },
                    },
                    {
                        astype: "SlotField",
                        label: "",
                        name: "slotbutton",
                        value: "",
                        placeholder: "",

                    },
                    ]
                }],
                IsValidDate: [],
                isedit: false,
                indexEdit: '',
                carryItems: [],
                oldTaskDesc: [],
                isOldTaskDesc: [],
                privilegeParams: null,
                holidaydates: [],
                isDatePanelVisible: false,
                selectedName: 'thisWeek',
                oldInputValue: []
            }
        },
        created: async function () {
            var VueObj = this;
            this.privilegeParams = `controller=Timesheet&action=`;
            this.DataLoaded = 1;
            await VueObj.GetStartEndDatesNew();
            await VueObj.GetUsersForTimesheet();
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            await VueObj.FetchData();
            //VueObj.checkHolidayDate()
            //VueObj.buttonShowHide()
            //this.dateselect = moment()
        },
        mounted: async function () {
            var VueObj = this;
            //VueObj.SaveAsDraft();
            document.addEventListener("click", function (e) {

                if (VueObj.isDatePanelVisible == true) {
                    var tagsMultiselect = document.querySelector('.date_select');
                    var more = document.querySelector('.openWeek')
                    if (e.target.parentNode.closest(".DatePopUp") == VueObj.$refs.weekModal || e.target == more || e.target.parentNode == more || e.target.parentNode == tagsMultiselect || e.target.parentNode == e.target.parentNode.closest(".DatePopUp") || e.target.className == 'date_select') return
                    VueObj.isDatePanelVisible = false

                }
                if (VueObj.addTaskShow) {
                    if (e.target == VueObj.$refs.taskModalOpen || e.target.parentNode == VueObj.$refs.taskModalOpen || e.target == VueObj.$refs.taskAddModal || e.target.parentNode.closest(".taskAddModal") == VueObj.$refs.taskAddModal) { return }
                    else {
                        VueObj.addTaskShow = false
                    }
                }



            });
        },
        computed: {
            computedTimesheetList() {
                return this.TimesheetList
            },
            computedBreakList() {
                return this.BreakList
            },
            computedLeaveList() {
                return this.LeaveList
            },
            filteredDeletedArray() {
                return this.deletedArray;
            },
            rowSums() {
                return this.TimesheetList.map(dataItem => this.rowSum(dataItem));
            },
            footerSums() {
                return this.totalData.map(dataItem => this.footerSums(dataItem));
            }
        },
        methods: {
            checkDescription(e) {
                if (e.target.value.match(/^\s*$/)) {
                    e.target.value = ''
                }
            },
            timeMask(value) {
                var s = value.target.value.split(":");
                // if(s[0]){
                //     console.log("time1",s[0])
                // }
                // if(s[1]){
                //     console.log("time1",s[1])
                // }

            },
            toggleDatePanel() {
                this.isDatePanelVisible = !this.isDatePanelVisible;
            },
            async checkHolidayDate() {
                var VueObj = this;
                var fDate = VueObj.StartDate;
                var tDate = VueObj.EndDate;
                VueObj.holidaydates = []
                var url = `dateFrom=${fDate}&dateTo=${tDate}`
                const {
                    data
                } = await DataService.GetHolidayDate(url)
                VueObj.holidaydates = data
            },
            checkLegendsDates(dates) {
                let matchDates = this.holidaydates.find((element) => getDateInFormat(element.DAY_DATE) == getDateInFormat(dates))
                return matchDates ? true : false
            },

            strToMins(t) {
                var s = t.split(":");
                return Number(s[0]) * 60 + Number(s[1]);
            },
            // minsToStr(t) {
            //     return Math.trunc(t / 60) + ':' + ('00' + t % 60).slice(-2);
            // },

            convertH2M(timeInHour) {
                var timeParts = timeInHour.split(":");
                return Number(timeParts[0]) * 60 + Number(timeParts[1]);
            },
            setOldValue(index, event) {
                let obj = this.oldInputValue.find((item) => item.index === index)
                if (!obj) {
                    this.oldInputValue.push({
                        value: event.target.value,
                        index: index
                    })
                }

            },
            async validateTimeInput(data, item, index) {
                let oldValue = this.oldInputValue.find((res) => res.index == item)
                var regex = /^([01][0-9]|2[0-3]):[0-5][0-9]$/;
                if (typeof Number(data[item]) == 'number' && data[item] < this.convertH2M("24:00") && !regex.test(data[item])) {
                    if (data[item] < 13) {
                        let hours = data[item] > 0 ? `${data[item].toString().length > 1 ? `${data[item]}` : `${0}${data[item]}`}` : '00'
                        data[item] = hours + ':00'
                    } else {
                        let format2 = (n) => `0${n / 60 ^ 0}`.slice(-2) + ':' + ('0' + n % 60).slice(-2)
                        data[item] = format2(data[item])
                    }

                }

                if (!regex.test(data[item])) {
                    this.ShowAlert(this.$t("validTime"), "warning", true, "Alert");
                    //alert("Please enter time in HH:MM .");
                    data[item] = oldValue.value ? oldValue.value : '00:00';
                }

                await this.SumRowColTotalList();
                //check column sum and if <24 hours then set old value
                var result = this.strToMins("24:00") - this.strToMins(this.totalData[index].value);
                if (result <= 0) {
                    data[item] = oldValue.value ? oldValue.value : '00:00';
                    await this.SumRowColTotalList();
